// == Import : npm
import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import store from './store';

import App from './containers/App';

// == Render
// 1. ÃlÃ©ment React racine (celui qui contient l'ensemble de l'app)
//    => crÃ©e une structure d'objets imbriquÃ©s (DOM virtuel)
const rootReactElement = (
  <Provider store={store}>
    <App />
  </Provider>
);
// 2. La cible du DOM (lÃ  oÃ¹ la structure doit prendre vie dans le DOM)
const target = document.getElementById('root');
// 3. DÃ©clenchement du rendu de React (virtuel) => DOM (page web)
render(rootReactElement, target);